.navbar {
    background: linear-gradient(90deg, rgb(28, 27, 27) 0%, rgb(26, 23, 23) 100%);
    height: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 1.2rem;
    position: sticky;
    top: 0;
    z-index: 999;
  }
  
  .navbar-container {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 80px;
    max-width: 1500px;
  }
  
  .navbar-logo {
    color: #fff;
    justify-self: start;
    margin-left: 0px;
    cursor: pointer;
    text-decoration: none;
    font-size: 1.2rem;
    display: flex;
    align-items: center;
  }
  
  .nav-menu {
    display: grid;
    grid-template-columns: repeat(4, auto);
    grid-gap: 10px;
    list-style: none;
    text-align: center;
    width: 60vw;
    justify-content: end;
    margin-right: 2rem;
  }
  
  .nav-item {
    height: 100px;
  }
  
  .nav-title1{
    margin-left:4px;
    font-weight: bold;
    letter-spacing: 1px;
    line-height: 27px;
    font-size: 22px;
  }
  
  .nav-title2{
    margin:0;
    font-size:14px;
  }
  
  .nav-links {
    color: #fff;
    display: flex;
    align-items: center;
    text-decoration: none;
    margin-top: 1.5rem;
    padding: 0.5rem 1rem;
  }
  
  .nav-links:hover {
    border-bottom: 4px solid #fff;
    transition: all 0.2s ease-out;
  }
  
  .fa-bars {
    color: #fff;
  }
  
  .menu-icon {
    display: none;
  }
  
  .doc_infor_detail{
    position:absolute;
    color:black;
    left: 130px;
    top:5px;
    width:52%;
    height:90%;
    margin:0;
    /* background-color:pink; */
  }
  
  .doc_location_detail{
    font-size:13px;
    line-height: 25px;
    width:100%;
    height:25px;
    margin:0;
  }
  
  @media screen and (max-width: 960px) {
    .NavbarItems {
      position: relative;
    }
  
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 91vh;
      padding-bottom: 40vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
    }
  
    .nav-menu.active {
      display:flex;
      background: #242222;
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-links {
      margin:0 0 0 15%;
      padding: 1rem;
      width: 70%;
      display: table;
      border: 2px solid #fff;
      border-radius: 8px;
    }
  
    .menu-icon {
      display: block;
      position: absolute;
      bottom:2.8rem;
      right: 0;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
    }
  
    .fa-times {
      color: #fff;
      font-size: 2rem;
    }
  }